    transition: all 0.5s linear;
    content: '';
    width: 0%;
    height: 100%;
    background: #80b7ff46;
    position: absolute;
    top: 0;
    left: 0;
} */
/* .cmd-item:hover:before {
    background: #80b7ff46;
    width: 100%;
} */

/* ‰øÆÂ§çmarkdown‰ª£Á†ÅÂ∏¶ÊªöÂä®Êù°ÈóÆÈ¢ò */
.vuepress-markdown-body {
    overflow: hidden;
}
</style>
